<template>
  <v-app >
    <v-app-bar app color="light" height="80" dark>
      <v-container>

        <v-row>
            <v-col cols="2">
              <v-switch v-model="online" @change="isOnlineUserInSite()" :true-value="online" :color="online ? 'success' : 'error'" :label="`Статус онлайна: ${online.toString()}`"></v-switch>
            </v-col>
            <v-divider class="my-2" vertical></v-divider>
            <v-col cols="3" >
              <div class="d-flex">
                <v-text-field v-model="pApiKey" type="password" label="Введите ключ Api" dense hide-details></v-text-field>
                <v-btn @click="addApiKey()" color="success" icon large><v-icon>mdi-content-save-outline</v-icon></v-btn>
              </div>
            </v-col>
            <v-divider class="my-2" vertical></v-divider>
            <v-col cols="2">
              <v-btn @click="deleteSavePrice()" color="error" block >Удалить все цены<v-icon>mdi-delete</v-icon></v-btn>
            </v-col>
            <v-divider class="my-2" vertical></v-divider>
            <v-col cols="2">
              <span>{{ statusTakeItem == 2 ? 'Отдай боту предмет' : statusTakeItem == 4 ? 'Забери предмет' : 'Нечего передавать / принимать'}}</span>
              <!-- <v-btn @click="checkItemsOnBot()" :loading="loadingCheck" :color="isItemsOnBot ? 'success' : 'error'">Проверка приема/передачи вещи</v-btn><br> -->
            </v-col>
        </v-row>
      </v-container>
    </v-app-bar>
    <v-main style="background: #1e1e1e">
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>

import { mapState, mapActions, mapMutations } from 'vuex';
// import API_KEY from './db.json'
  export default {
    name: 'App',

    data() {
      return {
        pApiKey: '',
        loadingCheck: false,
        // isOnline: false,
        };
    },
    computed:{
      ...mapState(["online", "isItemsOnBot", "statusTakeItem"])
    },
    mounted(){
      // console.log(API_KEY);
      // var temp = this.$cookies.get("API_KEY");
      this.pApiKey = localStorage.getItem('API_KEY');
      if(this.pApiKey){
        this.MUTATE_API_KEY(this.pApiKey);
      }
      this.isOnlineUserInSite();
    },  
    methods:{
      ...mapActions(["GET_API", "PING_ONLINE", "CHECK_ITEMS_ON_BOT"]),
      ...mapMutations(["MUTATE_API_KEY"]),
      isOnlineUserInSite(){
      this.PING_ONLINE();
      setInterval(() => {
        this.PING_ONLINE();
      }, 180000);
      },  
      addApiKey(){
        localStorage.setItem('API_KEY',this.pApiKey);

        // this.$cookies.set('API_KEY',this.pApiKey, 60 );
      },
      async checkItemsOnBot(){
        this.$refs.audio.play();
        // this.loadingCheck = true;
        // await this.CHECK_ITEMS_ON_BOT();
        // this.loadingCheck = false;
      }, 
      deleteSavePrice(){
        localStorage.clear();
        localStorage.setItem('API_KEY',this.pApiKey);

      },  
    }
};
</script>
